<template>
        <div class="col-sm-10" style="">
            <div class="head">
                <br>
                <p>账号信息</p>
            </div>
            <br>
            <div class="user-info">
                <form action="" class="" enctype="multipart/form-data" method="post" id="MyUserForm">
                    <div class="form-group">
                        <label for="user-name" class="control-label col-sm-4">邮箱</label>
                        <div class="col-sm-4">
                            <input type="text" class="form-control" id="user-name" readonly="readonly" v-model="userMessage.userEmail">
