                vm.userMessage = response.data.data;
            }).catch(function () {
                window.location.href= '/index';
            });
        },
        methods: {
            submitEvent: function() {
                
            }
        }
    }
</script>

<style>
    #submit{
        margin-top: 10px;
    }
</style>
